import styled from 'styled-components';
import { H6 } from '../../components/common/Typography';

interface ButtonStyle {
  width?: string;
  padding?: string;
  fontSize?: string;
}

interface AssetsListType {
  marginBottom?: string;
  maxWidth?: string;
  justifyContent?: string;
}

export const Wrapper = styled.div`
  padding-top: 275px;
  @media screen and (max-width: 991px) {
    padding: 142px 35px 40px 35px;
  }
  @media screen and (max-width: 550px) {
    padding: 142px 15px 80px 15px;
    h2 {
      font-size: 24px;
      margin-bottom: 30px;
    }
  }
`;

export const Container = styled.div`
  max-width: 1188px;
  margin: 0 auto;
  .card-panel {
    margin: 51px 0;
  }
  @media screen and (max-width: 991px) {
    padding-bottom: 50px;
    .card-panel {
      .partner-card {
        margin-bottom: 25px;
      }
    }
  }
  @media screen and (max-width: 768px) {
    padding-left: 20px;
    padding-right: 20px;
    .partner-card {
      width: 45%;
      h5 {
        height: auto;
      }
    }
  }
  @media screen and (max-width: 550px) {
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 0;
    .card-panel {
      .partner-card {
        width: 112px;
        h5 {
          font-size: 14px;
          margin-bottom: 12px !important;
          margin-top: 12px !important;
        }
      }
      button {
        font-size: 14px;
        padding: 7px;
      }
    }
  }
`;

export const AssetsList = styled.div<AssetsListType>`
  max-width: ${(props) => props.maxWidth || '750px'};
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  margin-bottom: ${(props) => props.marginBottom};
  justify-content: ${(props) => props.justifyContent || 'space-between'};
  .partner-card {
    margin-bottom: 103px;
  }
  &.first-assets-list {
    margin-top: 157px;
    margin-bottom: 80px;
  }
  &.assets-list-btn {
    margin-top: 78px;
  }
  @media screen and (max-width: 1440px) {
    &.first-assets-list {
      margin-bottom: 140px;
    }
  }
  @media screen and (max-width: 768px) {
    justify-content: space-between;
    .partner-card {
      width: 45%;
    }
    &.first-assets-list {
      margin-bottom: 40px;
      margin-top: 0px;
    }
    &.assets-list-btn {
      margin-top: 40px;
      margin-bottom: 100px;
    }
  }
  @media screen and (max-width: 550px) {
    &.first-assets-list {
      margin-bottom: 0px;
    }
    &.assets-list-btn {
      margin-top: 32px;
      margin-bottom: 32px;
    }
    .partner-card {
      margin-bottom: 50px;
    }
  }
`;

export const SubtitleWrapper = styled(H6)`
  margin: 0 auto;
  max-width: 947px;
  &.font-italic {
    font-style: italic;
  }
  .font-italic {
    font-style: italic;
    font-weight: 500;
  }
  .text-yellow {
    color: #e2ae39;
  }
  @media screen and (max-width: 550px) {
    font-size: 14px;
    &.text-click {
      margin-top: -12px;
    }
  }
`;

export const Button = styled.button<ButtonStyle>`
  padding: ${(props) => props.padding || '10px 20px'};
  width: ${(props) => props.width || '100%'};
  margin: auto;
  background: #8f8f8f;
  border-radius: 25px;
  text-align: center;
  font-size: ${(props) => props.fontSize || '24px'};
  font-weight: 700;
  color: #ffffff;
  border: none;
  line-height: 'inherit';
  transition: all 0.3s ease-in;

  &:hover {
    background: #e2ae39;
    color: #000000;
  }

  @media screen and (max-width: 768px) {
    font-size: 18px;
    border-radius: 6px;
    padding: 15px 30px;
    &.btn-card {
      width: 100%;
    }
  }
  @media screen and (max-width: 425px) {
    width: 101px;
    font-size: 12px;
    padding: 10px 5px;
    &.btn-top {
      width: 159px;
    }
  }
`;

export const ContentWrapper = styled.div`
  max-width: 1033px;
  margin: 64px auto;
  padding-top: 114px;

  .coming-soon {
    color: #ff0000;
  }

  ul {
    padding-left: 2.5rem;
  }

  ul li {
    font-size: 24px;
  }
`;
