import { useEffect, useMemo, useState } from 'react';
import { useMedia } from 'react-use';
import { useWalletConnect } from '../../hooks/WalletConnect';
import AlertModal from '../../components/Modals/AlertModal';
import { Wrapper, Container, SubtitleWrapper, Button } from './Redeem.styled';
import { H2 } from '../../components/common/Typography';
import AssetCard from '../../components/AssetCard';
import RedeemModal from '../../components/Modals/RedeemModal';
import { Data, Constr } from 'lucid-cardano';
import axios from 'axios';

const RedeemPage = () => {
  const [modalData, setModalData] = useState('');
  const [currentModal, setCurrentModal] = useState(1);
  const [showRedeemModal, setShowRedeemModal] = useState(false);
  const { lucid } = useWalletConnect();
  const [showAlert, setShowAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [supplyData, setSupplyData] = useState({});
  const cardData = useMemo(
    () => [
      {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        title: 'Car Parts<br/>Supply: ' + supplyData?.parts + '/30<br/>Total: 3,000 $MD',
        isVideo: true,
        image: '/assets/videos/car-rank.mp4',
        modalText:
          'By Agreeing to this transaction, you are agreeing to redeem 3,000 MD Tokens for 1 Random Car Part. All transactions are final and irreversible.'
      },
      {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        title: 'Mystery<br/>Supply: ' + supplyData?.random + '/10<br/>Total: 7,000 $MD',
        isVideo: false,
        image: '/assets/images/mystery.png',
        modalText:
          'By Agreeing to this transaction, you are agreeing to redeem 7,000 MD Tokens for Mystery Asset. All transactions are final and irreversible.'
      },
      {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        title: 'MD License<br/>Supply: ' + supplyData?.licences + '/5<br/>Total: 30,000 $MD',
        isVideo: false,
        image: '/assets/images/MDCC-CARD.gif',
        modalText:
          'By Agreeing to this transaction, you are agreeing to redeem 30,000 MD Tokens for MD License. All transactions are final and irreversible.'
      }
    ],
    [supplyData]
  );

  const handleRedeem = () => {
    const markeplaceScript = {
      type: 'PlutusV2',
      script:
        ''
    };
    let utxoNumber = 2,
      price = 30000;

    if (currentModal === 1) {
      utxoNumber = 0;
      price = 3000;
    } else if (currentModal === 2) {
      utxoNumber = 1;
      price = 7000;
    }
    async function redeemCar() {
      const walletName = localStorage.getItem('active-wallet-name')?.replaceAll('"', '');
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const api = await window.cardano[walletName].enable();
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      lucid.selectWallet(api);

      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const marketplaceAddress = lucid.utils.validatorToAddress(markeplaceScript);
      const Redeemer = Data.to(new Constr(1, []));
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      const utxo = (await lucid.utxosAt(marketplaceAddress)).find(
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        (item: any) => Data.from(item.datum).fields[8] == 0
      );

      // console.log(utxo); //just for testing
      try {
        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        const tx = await lucid
          .newTx()
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          .collectFrom([utxo], Redeemer)
          // eslint-disable-next-line @typescript-eslint/ban-ts-comment
          // @ts-ignore
          .attachSpendingValidator(markeplaceScript)
          .payToAddress(
            'addr1q93y76vvypcseexf3y5e4crncu4ahl4ase7cy0et4zz0pxhxe42rgj258e474t3x9ktnlhqssmqqgqse0dl2ap4gsvsqx4sgp8',
            {
              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
              // @ts-ignore
              lovelace: 2000000,
              // eslint-disable-next-line @typescript-eslint/ban-ts-comment
              // @ts-ignore
              ['772791eb3f4b92874a49d487375a90db631988291c1a643b817668ca4d616420446f6720546f6b656e']:
                price * 100000000
            }
          )
          .complete({ nativeUplc: true });

        const signedTx = await tx.sign().complete();
        const txHash = await signedTx.submit();
        // console.log(txHash);
        setAlertMessage('Transaction was successful');
        setShowAlert(true);
      } catch (err) {
        setAlertMessage('Transaction was unsuccessful');
        setShowAlert(true);
      }
    }
    redeemCar();
    setShowRedeemModal(false);
  };

  const getSupplyData = async () => {
    const { data } = await axios.get(
      'https://5zxg58vs0e.execute-api.eu-west-2.amazonaws.com/default/MDCC-getSCStats'
    );
    setSupplyData(data);
  };
  useEffect(() => {
    getSupplyData();
  }, []);

  return (
    <Wrapper className="page">
      <Container>
        <H2 fontWeight={700} textAlign="center">
          REDEEM YOUR TOKENS
        </H2>
        <SubtitleWrapper textAlign="center">
          You can redeem your tokens for a Car Part, MD License or a Mystery Asset. All assets are
          random! Mystery Asset can be any assets from MDCC or our partnership projects.
        </SubtitleWrapper>
        <div className="card-panel d-flex justify-content-around flex-wrap gap-5">
          {cardData.map((item, index) => (
            <div key={index} className="partner-card d-flex flex-column align-item-center">
              <AssetCard
                src={item.image}
                isVideoCard={item.isVideo}
                title={item.title}
                width={'299px'}
              />
              <Button
                width="100%"
                padding="20px 58px"
                fontSize="32px"
                className="btn-card"
                onClick={() => {
                  setModalData(item.modalText);
                  setCurrentModal(index + 1);
                  setShowRedeemModal(true);
                }}
              >
                REDEEM
              </Button>
            </div>
          ))}
        </div>
      </Container>
      <RedeemModal
        message={modalData}
        show={showRedeemModal}
        onClose={() => setShowRedeemModal(false)}
        handleAgree={handleRedeem}
      />
      <AlertModal
        show={showAlert}
        onClose={() => {
          setShowAlert(false);
        }}
        message={alertMessage}
      />
    </Wrapper>
  );
};

export default RedeemPage;
