import styled from 'styled-components';
import { H5 } from '../common/Typography';
import { useMedia } from 'react-use';
import parse from 'html-react-parser';

interface CardType {
  width: string;
}

const CardWrapper = styled.div<CardType>`
  width: ${(props) => props.width};
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-bottom: 30px;
  .image-panel {
    width: 100%;
    padding-top: 100%;
    position: relative;
  }
  video,
  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    height: 300px;
    border-radius: 25px;
    margin-bottom: 30px;
    &.md-license-image {
      object-fit: cover;
    }
    @media screen and (max-width: 768px) {
      width: 100%;
      height: 100%;
      margin-bottom: 0px;
    }
  }

  @media screen and (max-width: 768px) {
    margin-bottom: 0;
    width: 100%;
    h5 {
      text-align: center;
      font-size: 24px;
      height: 60px;
    }
  }
  @media screen and (max-width: 550px) {
    h5 {
      font-size: 16px;
      height: 40px;
      font-weight: 700;
      margin-bottom: 10px !important;
    }
  }
`;

interface AssetCardProps {
  src: string;
  title: string;
  isVideoCard?: boolean;
  width?: string;
}

const AssetCard = ({ src, title, isVideoCard = false, width = '327px' }: AssetCardProps) => {
  const isMobile = useMedia('(max-width: 425px)');
  return (
    <CardWrapper className="asset-card" width={width}>
      <div className="image-panel">
        {isVideoCard ? (
          <video autoPlay loop muted>
            <source src={src} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        ) : (
          <img
            className={
              'card-img ' +
              (title === 'MD License'
                ? 'md-license-image'
                : title === 'MD License<br/>Supply: 5/5<br/>Total: 30,000 $MD'
                ? 'md-license-image'
                : '')
            }
            src={src}
            alt={title}
          />
        )}
      </div>
      <H5 fontWeight={700} className="card-name my-4 text-center">
        {parse(title)}
      </H5>
    </CardWrapper>
  );
};

export default AssetCard;
